.grid1 {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.grid2 {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 1rem;
}

.button {
    background-color: #4CAF50;
    border: none;
    color: white;
    padding: 15px 32px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 16px;
    margin: 4px 2px;
    cursor: pointer;
}




